:root {
  --bg-color: #113344;
  --header-color: floralwhite;
  --detail-color: white;
  --text-color: linear-gradient(
    to right,
    var(--heat-value) 0%,
    var(--cool-value) 100%
  );
  --secondary-color: rgba(255, 255, 255, 1);
  --cool-gradient: linear-gradient(to right, #30cfd0 0%, #330867 100%);
  --adaptive-color: rgba(255, 255, 255, 0);
  --bg-color: rgba(0, 0, 0, 0.3);
}

body {
  display: flex;
  flex-direction: column;
  width: 100vw;
  height: 100vh;
  background-image: linear-gradient(to top, #000000 0%, #232023 100%);
}

#content-div {
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-rows: 5% 95%;
  overflow: hidden;
}

#header {
  grid-row: 1 / 2;
}

#main-content-div {
  display: flex;
  margin: auto;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 90%;
  height: 100%;
}

#display {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  width: 100%;
  height: 60%;
  font:
    3rem Inconsolata,
    monospace;
  text-shadow: 0 0 5px #c8c8c8;

  /* Screen appearance */
  background: linear-gradient(to top, #222, #444);
  border: 1rem solid #111; /* thick dark frame */
  border-top-left-radius: 1rem;
  border-top-right-radius: 1rem;
  /* box-shadow: */
  /*   inset 0 0 20px rgba(0, 255, 0, 0.3), */
  /*   0 0 30px rgba(0, 0, 0, 0.7), */
  /*   0 0 10px rgba(0, 255, 0, 0.2); */
  /**/
  /* Optional: slight reflection overlay using pseudo-element if desired later */
}

#terminal {
  position: relative; /* add this */
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 30%;
  background:
    radial-gradient(hsla(0, 0%, 0%, 0), hsla(0, 0%, 12%, 1)),
    linear-gradient(90deg, green 1px, transparent 1px),
    linear-gradient(dimgrey 1px, grey 1px);
  background-size:
    100%,
    2px 1px,
    1px 2px;
  color: white;
  font:
    1.3rem Inconsolata,
    monospace;
  text-shadow: 0 0 5px #c8c8c8;
  overflow: hidden;
}

#terminal::after {
  content: "";
  position: absolute; /* change from relative to absolute */
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1; /* set above default content */

  background: repeating-linear-gradient(
    0deg,
    rgba(0, 0, 0, 0.15),
    rgba(0, 0, 0, 0.15) 1px,
    transparent 1px,
    transparent 2px
  );
  pointer-events: none;
}

#terminal-span {
  position: relative; /* needed for z-index to apply */
  width: 100%;
  z-index: 0;
}

#terminal-span span {
  display: block;
}

#controls {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 3rem;
  padding-right: 3rem;
  width: 100%;
  height: 20%;
  background:
    radial-gradient(hsla(0, 0%, 0%, 0), hsla(0, 0%, 0%, 1)),
    linear-gradient(90deg, dimgrey 1px, transparent 1px),
    linear-gradient(dimgrey 1px, grey 1px);
  background-size:
    100%,
    2px 1px,
    1px 2px;
}

#sonar {
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  grid-template-rows: repeat(10, 1fr);
  position: relative;
  width: 30rem;
  height: 7.5rem;
  border-radius: 2rem;
  background-color: black;
  overflow: hidden;
  color: greenyellow;
  font:
    3rem Inconsolata,
    monospace;
  text-shadow: 0 0 5px #c8c8c8;
  box-shadow:
    0 0 0 1px hsla(0, 0%, 0%, 0.5),
    inset 0 0 0 1px hsla(0, 0%, 100%, 0.15);
  background:
    repeating-radial-gradient(
      at center bottom,
      transparent 0 23px,
      #287a33 24px 26px,
      transparent 27px
    ),
    linear-gradient(transparent 49.75%, #0e4c42 0 50.25%, transparent 0) 50%
      no-repeat,
    linear-gradient(90deg, transparent 49.75%, #0e4c42 0 50.25%, transparent 0)
      50% no-repeat,
    linear-gradient(#333, #111);
  background-size:
    cover,
    218px 218px,
    218px 218px,
    100%;
}

#sonar::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 55rem;
  height: 14rem;
  border-radius: 2rem;
  background: repeating-linear-gradient(
    0deg,
    rgba(0, 0, 0, 0.15),
    rgba(0, 0, 0, 0.15) 1px,
    transparent 1px,
    transparent 2px
  );
}

#sonar-sweep {
  position: absolute;
  bottom: 0;
  left: 50%; /* start at center horizontally */
  width: 0%;
  height: 0%;
  background: radial-gradient(
    ellipse at center bottom,
    hsla(145, 50%, 40%, 0.7),
    rgba(0, 0, 0, 0) 70%
  );
  transform: translateX(-50%);
  transform-origin: center bottom;
  opacity: 0;
  /* background-color: red; */
}

.sonar-dot {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  border-radius: 50%;
  width: 40%; /* adjusted for sonar dot size */
  height: 40%;
  background: radial-gradient(circle, limegreen 0%, green 70%, darkgreen 100%);
  box-shadow: 0 0 8px 2px limegreen;
  opacity: 0;
  transition:
    opacity 0.5s ease,
    transform 0.5s ease;
}

.sonar-dot[highlighted="true"] {
  opacity: 1;
  transition: opacity 0.5s ease;
}

@keyframes linear-sweep {
  0% {
    width: 0%;
    height: 0%;
    opacity: 1;
  }
  100% {
    width: 200%;
    height: 200%;
    opacity: 0;
  }
}

#radar-div {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 25%;
  height: 100%;
  overflow: hidden;
}

#radar {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative; /* make ::after position relative to this */
  overflow: hidden;
  margin: -117px;
  width: 148px;
  height: 148px;
  border-radius: 50%;
  box-shadow:
    0 0 0 1px hsla(0, 0%, 0%, 0.5),
    inset 0 0 0 1px hsla(0, 0%, 100%, 0.15);
  background:
    repeating-radial-gradient(
      transparent 0 23px,
      #287a33 24px 26px,
      #0000 27px
    ),
    linear-gradient(transparent 49.75%, #0e4c42 0 50.25%, transparent 0) 50%
