    linear-gradient(90deg, transparent 49.75%, #0e4c42 0 50.25%, transparent 0)
      50% no-repeat,
    linear-gradient(#333, #111);
  background-size:
    100%,
    218px 218px,
    218px 218px,
    100%;
}

#radar::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 50%;
  width: 100%;
  height: 100%;
  background: repeating-linear-gradient(
    0deg,
    rgba(0, 0, 0, 0.15),
    rgba(0, 0, 0, 0.15) 1px,
    transparent 1px,
    transparent 2px
  );
}

#radar-dot-div {
  display: grid;
  grid-template-columns: repeat(10, 0.7fr);
  grid-template-rows: repeat(10, 0.7fr);
  width: 70%;
  height: 70%;
}

.radar-dot {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  border-radius: 50%;
  width: 80%; /* adjusted for sonar dot size */
  height: 80%;
  background: radial-gradient(circle, limegreen 0%, green 70%, darkgreen 100%);
  box-shadow: 0 0 8px 2px limegreen;
  opacity: 0;
  transition:
    opacity 0.5s ease,
    transform 0.5s ease;
}

.radar-dot[highlighted="true"] {
  opacity: 1;
  transition: opacity 0.9s ease;
}

::selection {
  background: #dddddd;
  text-shadow: none;
}

#radar-sweep {
  position: absolute;
  opacity: 0;
  top: 50%;
  left: 50%;
  box-sizing: border-box;
  border-right: solid 1px hsla(145, 50%, 40%, 0.3);
  width: 108px;
  height: 108px;
  border-radius: 100% 0 0 0;
  transform: translate(-100%, -100%) rotate(0deg);
  transform-origin: 100% 100%;
  background: linear-gradient(
    50deg,
    rgba(34, 34, 34, 0) 56%,
    hsla(145, 50%, 40%, 1)
  );
  animation: radial-sweep 5s infinite linear;
}

@keyframes radial-sweep {
  to {
    transform: translate(-100%, -100%) rotate(360deg);
  }
}

#type-prompt {
  font-size: 4rem;
}

#place-fleet {
  padding: 0; /* Fixed invalid padding */
  text-align: center;
  transition: transform 0.5s ease-in-out;
}

#quick-deploy {
  padding-bottom: 3rem;
  text-align: center;
  transition: transform 0.5s ease-in-out;
}

#analog {
  width: 100%;
  height: 20%;
  background-color: red;
}

.name {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 3rem;
}

.player-div {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 45%;
  height: 100%;
}

.board {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 80%;
  height: 80%;
}

.board-row {
  display: flex; /* THIS fixes squares layout horizontally */
  width: calc(2.2 rem * 10);
  height: 10%; /* 10 rows = 10% height each */
}

.square {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 2.2rem; /* each square is 10% of board width */
  height: 2.2rem;
  outline: 0.1rem solid black;
  transition: shake 0.2s ease-in-out;
  animation-fill-mode: both;
  z-index: 2;
}

@keyframes shake {
  0% {
    transform: translateX(0);
  }
  25% {
    transform: translateX(-3px);
  }
  50% {
    transform: translateX(3px);
  }
  75% {
    transform: translateX(-3px);
  }
  100% {
    transform: translateX(0);
  }
}

.ship-img {
  width: 100%;
  height: 100%;
  display: block;
  z-index: 1;
}

.ship-div {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  overflow: visible;
  mix-blend-mode: multiply;
  z-index: 1;
  /* outline: 2px solid red; */
}

.ship-img {
  pointer-events: none !important;
}

.ship-img[player-ship="false"] {
  opacity: 0;
}

#player-carrier {
  outline: 3px solid transparent;
}

#player-submarine {
  outline: 3px solid transparent;
}

#player-destroyer {
  outline: 3px solid transparent;
}

#player-cruiser {
  outline: 3px solid transparent;
}

#player-battleship {
  outline: 3px solid transparent;
}

.square[toggled="true"] {
  background-image: src(
    "https://lapsedtime.com/wp-content/uploads/2016/03/water.gif"
  );
  background-position: cover;
  background-blend-mode: multiply;
}

#toggle-x {
  position: absolute;
  object-fit: contain;
  font-size: 2rem;
  color: maroon;
}

#toggle-bomb {
  position: absolute;
  width: 2rem;
  height: 2rem;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-size: contain;
  background-repeat: no-repeat;
  mix-blend-mode: overlay; /* blend with backdrop */
  opacity: 0.8;
}

#ship-select-div {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  height: 50%;
}

