AAmB;EACnB,kBAAkB;EAClB,iBAAiB;EACjB,wBAAwB;EACxB,UAAU;EACV,4BAA4B;AAC9B;;AAEA;EACE,+BAA+B;AACjC;;AAEA;EACE,UAAU;AACZ;;AAEA;EACE,8BAA8B;AAChC;;AAEA;EACE,8BAA8B;AAChC;;AAEA;EACE,8BAA8B;AAChC;;AAEA;EACE,8BAA8B;AAChC;;AAEA;EACE,8BAA8B;AAChC;;AAEA;EACE;;GAEC;EACD,0BAA0B;EAC1B,+BAA+B;AACjC;;AAEA;EACE,kBAAkB;EAClB,mBAAmB;EACnB,eAAe;EACf,aAAa;AACf;;AAEA;EACE,kBAAkB;EAClB,WAAW;EACX,YAAY;EACZ,yDAA+C;EAC/C,wBAAwB;EACxB,4BAA4B;EAC5B,uBAAuB,EAAE,wBAAwB;EACjD,YAAY;AACd;;AAEA;EACE,aAAa;EACb,2BAA2B;EAC3B,mBAAmB;EACnB,WAAW;EACX,WAAW;AACb;;AAEA,mBAAmB;AACnB,mBAAmB;AACnB,oBAAoB;AACpB,sBAAsB;AACtB,kBAAkB;AAClB,MAAM;AACN,GAAG;AACH;EACE,aAAa;EACb,uBAAuB;EACvB,mBAAmB;EACnB,WAAW;EACX,UAAU;EACV,iBAAiB;EACjB,wBAAwB;EACxB,4BAA4B;AAC9B","sourcesContent":["/** radar css borrowed from \"https://codepen.io/thebabydino/pen/AqbWeE\" **/\n\n* {\n  padding: 0;\n  margin: 0;\n  box-sizing: border-box;\n  user-select: none;\n}\n\n/* http://meyerweb.com/eric/tools/css/reset/ */\nhtml,\nbody,\ndiv,\nspan,\napplet,\nobject,\niframe,\nh1,\nh2,\nh3,\nh4,\nh5,\nh6,\np,\nblockquote,\npre,\na,\nabbr,\nacronym,\naddress,\nbig,\ncite,\ncode,\ndel,\ndfn,\nem,\nimg,\nins,\nkbd,\nq,\ns,\nsamp,\nsmall,\nstrike,\nstrong,\nsub,\nsup,\ntt,\nvar,\nb,\nu,\ni,\ncenter,\ndl,\ndt,\ndd,\nol,\nul,\nli,\nfieldset,\nform,\nlabel,\nlegend,\ntable,\ncaption,\ntbody,\ntfoot,\nthead,\ntr,\nth,\ntd,\narticle,\naside,\ncanvas,\ndetails,\nembed,\nfigure,\nfigcaption,\nfooter,\nheader,\nhgroup,\nmenu,\nnav,\noutput,\nruby,\nsection,\nsummary,\ntime,\nmark,\naudio,\nvideo {\n  margin: 0;\n  padding: 0;\n  border: 0;\n  font-size: 100%;\n  font: inherit;\n  vertical-align: baseline;\n}\n\n/* HTML5 display-role reset for older browsers */\narticle,\naside,\ndetails,\nfigcaption,\nfigure,\nfooter,\nheader,\nhgroup,\nmenu,\nnav,\nsection {\n  display: block;\n}\n\nol,\nul {\n  list-style: none;\n}\n\nblockquote,\nq {\n  quotes: none;\n}\n\nblockquote::before,\nblockquote::after,\nq::before,\nq::after {\n  content: \"\";\n  content: none;\n}\n\ntable {\n  border-collapse: collapse;\n  border-spacing: 0;\n}\n\n:root {\n  --bg-color: #113344;\n  --header-color: floralwhite;\n  --detail-color: white;\n  --text-color: linear-gradient(\n    to right,\n    var(--heat-value) 0%,\n    var(--cool-value) 100%\n  );\n  --secondary-color: rgba(255, 255, 255, 1);\n  --cool-gradient: linear-gradient(to right, #30cfd0 0%, #330867 100%);\n  --adaptive-color: rgba(255, 255, 255, 0);\n  --bg-color: rgba(0, 0, 0, 0.3);\n}\n\nbody {\n  display: flex;\n  flex-direction: column;\n  width: 100vw;\n  height: 100vh;\n  background-image: linear-gradient(to top, #000000 0%, #232023 100%);\n}\n\n#content-div {\n  width: 100%;\n  height: 100%;\n  display: grid;\n  grid-template-rows: 5% 95%;\n  overflow: hidden;\n}\n\n#header {\n  grid-row: 1 / 2;\n}\n\n#main-content-div {\n  display: flex;\n  margin: auto;\n  flex-direction: column;\n  justify-content: center;\n  align-items: center;\n  width: 90%;\n  height: 100%;\n}\n\n#display {\n  display: flex;\n  flex-direction: row;\n  justify-content: space-evenly;\n  align-items: center;\n  width: 100%;\n  height: 60%;\n  font:\n    3rem Inconsolata,\n    monospace;\n  text-shadow: 0 0 5px #c8c8c8;\n\n  /* Screen appearance */\n  background: linear-gradient(to top, #222, #444);\n  border: 1rem solid #111; /* thick dark frame */\n  border-top-left-radius: 1rem;\n  border-top-right-radius: 1rem;\n  /* box-shadow: */\n  /*   inset 0 0 20px rgba(0, 255, 0, 0.3), */\n  /*   0 0 30px rgba(0, 0, 0, 0.7), */\n  /*   0 0 10px rgba(0, 255, 0, 0.2); */\n  /**/\n  /* Optional: slight reflection overlay using pseudo-element if desired later */\n}\n\n#terminal {\n  position: relative; /* add this */\n  display: flex;\n  flex-direction: column;\n  width: 100%;\n  height: 30%;\n  background:\n    radial-gradient(hsla(0, 0%, 0%, 0), hsla(0, 0%, 12%, 1)),\n    linear-gradient(90deg, green 1px, transparent 1px),\n    linear-gradient(dimgrey 1px, grey 1px);\n  background-size:\n    100%,\n    2px 1px,\n    1px 2px;\n  color: white;\n  font:\n    1.3rem Inconsolata,\n    monospace;\n  text-shadow: 0 0 5px #c8c8c8;\n  overflow: hidden;\n}\n\n#terminal::after {\n  content: \"\";\n  position: absolute; /* change from relative to absolute */\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 100%;\n  z-index: 1; /* set above default content */\n\n  background: repeating-linear-gradient(\n    0deg,\n    rgba(0, 0, 0, 0.15),\n    rgba(0, 0, 0, 0.15) 1px,\n    transparent 1px,\n    transparent 2px\n  );\n  pointer-events: none;\n}\n\n#terminal-span {\n  position: relative; /* needed for z-index to apply */\n  width: 100%;\n  z-index: 0;\n}\n\n#terminal-span span {\n  display: block;\n}\n\n#controls {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  padding-left: 3rem;\n  padding-right: 3rem;\n  width: 100%;\n  height: 20%;\n  background:\n    radial-gradient(hsla(0, 0%, 0%, 0), hsla(0, 0%, 0%, 1)),\n    linear-gradient(90deg, dimgrey 1px, transparent 1px),\n    linear-gradient(dimgrey 1px, grey 1px);\n  background-size:\n    100%,\n    2px 1px,\n    1px 2px;\n}\n\n#sonar {\n  display: grid;\n  grid-template-columns: repeat(10, 1fr);\n  grid-template-rows: repeat(10, 1fr);\n  position: relative;\n  width: 30rem;\n  height: 7.5rem;\n  border-radius: 2rem;\n  background-color: black;\n  overflow: hidden;\n  color: greenyellow;\n  font:\n    3rem Inconsolata,\n    monospace;\n  text-shadow: 0 0 5px #c8c8c8;\n  box-shadow:\n    0 0 0 1px hsla(0, 0%, 0%, 0.5),\n    inset 0 0 0 1px hsla(0, 0%, 100%, 0.15);\n  background:\n    repeating-radial-gradient(\n      at center bottom,\n      transparent 0 23px,\n      #287a33 24px 26px,\n      transparent 27px\n    ),\n    linear-gradient(transparent 49.75%, #0e4c42 0 50.25%, transparent 0) 50%\n      no-repeat,\n    linear-gradient(90deg, transparent 49.75%, #0e4c42 0 50.25%, transparent 0)\n      50% no-repeat,\n    linear-gradient(#333, #111);\n  background-size:\n    cover,\n    218px 218px,\n    218px 218px,\n    100%;\n}\n\n#sonar::after {\n  content: \"\";\n  position: absolute;\n  top: 0;\n  left: 0;\n  width: 55rem;\n  height: 14rem;\n  border-radius: 2rem;\n  background: repeating-linear-gradient(\n    0deg,\n    rgba(0, 0, 0, 0.15),\n    rgba(0, 0, 0, 0.15) 1px,\n    transparent 1px,\n    transparent 2px\n  );\n}\n\n#sonar-sweep {\n  position: absolute;\n  bottom: 0;\n  left: 50%; /* start at center horizontally */\n  width: 0%;\n  height: 0%;\n  background: radial-gradient(\n    ellipse at center bottom,\n    hsla(145, 50%, 40%, 0.7),\n    rgba(0, 0, 0, 0) 70%\n  );\n  transform: translateX(-50%);\n  transform-origin: center bottom;\n  opacity: 0;\n  /* background-color: red; */\n}\n\n.sonar-dot {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  position: relative;\n  border-radius: 50%;\n  width: 40%; /* adjusted for sonar dot size */\n  height: 40%;\n  background: radial-gradient(circle, limegreen 0%, green 70%, darkgreen 100%);\n  box-shadow: 0 0 8px 2px limegreen;\n  opacity: 0;\n  transition:\n    opacity 0.5s ease,\n    transform 0.5s ease;\n}\n\n.sonar-dot[highlighted=\"true\"] {\n  opacity: 1;\n  transition: opacity 0.5s ease;\n}\n\n@keyframes linear-sweep {\n  0% {\n    width: 0%;\n    height: 0%;\n    opacity: 1;\n  }\n  100% {\n    width: 200%;\n    height: 200%;\n    opacity: 0;\n  }\n}\n\n#radar-div {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  width: 25%;\n  height: 100%;\n  overflow: hidden;\n}\n\n#radar {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  position: relative; /* make ::after position relative to this */\n  overflow: hidden;\n  margin: -117px;\n  width: 148px;\n  height: 148px;\n  border-radius: 50%;\n  box-shadow:\n    0 0 0 1px hsla(0, 0%, 0%, 0.5),\n    inset 0 0 0 1px hsla(0, 0%, 100%, 0.15);\n  background:\n    repeating-radial-gradient(\n      transparent 0 23px,\n      #287a33 24px 26px,\n      #0000 27px\n    ),\n    linear-gradient(transparent 49.75%, #0e4c42 0 50.25%, transparent 0) 50%\n      no-repeat,\n    linear-gradient(90deg, transparent 49.75%, #0e4c42 0 50.25%, transparent 0)\n      50% no-repeat,\n    linear-gradient(#333, #111);\n  background-size:\n    100%,\n    218px 218px,\n    218px 218px,\n    100%;\n}\n\n#radar::after {\n  content: \"\";\n  position: absolute;\n  top: 0;\n  left: 0;\n  border-radius: 50%;\n  width: 100%;\n  height: 100%;\n  background: repeating-linear-gradient(\n    0deg,\n    rgba(0, 0, 0, 0.15),\n    rgba(0, 0, 0, 0.15) 1px,\n    transparent 1px,\n    transparent 2px\n  );\n}\n\n#radar-dot-div {\n  display: grid;\n  grid-template-columns: repeat(10, 0.7fr);\n  grid-template-rows: repeat(10, 0.7fr);\n  width: 70%;\n  height: 70%;\n}\n\n.radar-dot {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  position: relative;\n  border-radius: 50%;\n  width: 80%; /* adjusted for sonar dot size */\n  height: 80%;\n  background: radial-gradient(circle, limegreen 0%, green 70%, darkgreen 100%);\n  box-shadow: 0 0 8px 2px limegreen;\n  opacity: 0;\n  transition:\n    opacity 0.5s ease,\n    transform 0.5s ease;\n}\n\n.radar-dot[highlighted=\"true\"] {\n  opacity: 1;\n  transition: opacity 0.9s ease;\n}\n\n::selection {\n  background: #dddddd;\n  text-shadow: none;\n}\n\n#radar-sweep {\n  position: absolute;\n  opacity: 0;\n  top: 50%;\n  left: 50%;\n  box-sizing: border-box;\n  border-right: solid 1px hsla(145, 50%, 40%, 0.3);\n  width: 108px;\n  height: 108px;\n  border-radius: 100% 0 0 0;\n  transform: translate(-100%, -100%) rotate(0deg);\n  transform-origin: 100% 100%;\n  background: linear-gradient(\n    50deg,\n    rgba(34, 34, 34, 0) 56%,\n    hsla(145, 50%, 40%, 1)\n  );\n  animation: radial-sweep 5s infinite linear;\n}\n\n@keyframes radial-sweep {\n  to {\n    transform: translate(-100%, -100%) rotate(360deg);\n  }\n}\n\n#type-prompt {\n  font-size: 4rem;\n}\n\n#place-fleet {\n  padding: 0; /* Fixed invalid padding */\n  text-align: center;\n  transition: transform 0.5s ease-in-out;\n}\n\n#quick-deploy {\n  padding-bottom: 3rem;\n  text-align: center;\n  transition: transform 0.5s ease-in-out;\n}\n\n#analog {\n  width: 100%;\n  height: 20%;\n  background-color: red;\n}\n\n.name {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  font-size: 3rem;\n}\n\n.player-div {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  flex-direction: column;\n  width: 45%;\n  height: 100%;\n}\n\n.board {\n  position: relative;\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  align-items: center;\n  width: 80%;\n  height: 80%;\n}\n\n.board-row {\n  display: flex; /* THIS fixes squares layout horizontally */\n  width: calc(2.2 rem * 10);\n  height: 10%; /* 10 rows = 10% height each */\n}\n\n.square {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  position: relative;\n  width: 2.2rem; /* each square is 10% of board width */\n  height: 2.2rem;\n  outline: 0.1rem solid black;\n  transition: shake 0.2s ease-in-out;\n  animation-fill-mode: both;\n  z-index: 2;\n}\n\n@keyframes shake {\n  0% {\n    transform: translateX(0);\n  }\n  25% {\n    transform: translateX(-3px);\n  }\n  50% {\n    transform: translateX(3px);\n  }\n  75% {\n    transform: translateX(-3px);\n  }\n  100% {\n    transform: translateX(0);\n  }\n}\n\n.ship-img {\n  width: 100%;\n  height: 100%;\n  display: block;\n  z-index: 1;\n}\n\n.ship-div {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  position: absolute;\n  overflow: visible;\n  mix-blend-mode: multiply;\n  z-index: 1;\n  /* outline: 2px solid red; */\n}\n\n.ship-img {\n  pointer-events: none !important;\n}\n\n.ship-img[player-ship=\"false\"] {\n  opacity: 0;\n}\n\n#player-carrier {\n  outline: 3px solid transparent;\n}\n\n#player-submarine {\n  outline: 3px solid transparent;\n}\n\n#player-destroyer {\n  outline: 3px solid transparent;\n}\n\n#player-cruiser {\n  outline: 3px solid transparen