___CSS_LOADER_EXPORT___.push([module.id, `@media (max-width: 1200px) {
  html,
  body {
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
  }

  html {
    font-size: 50%; /* Reduces all rem-based font sizes to 50% of the default */
  }

  * {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    user-select: none;
  }

  #content-div {
    width: 100vw;
    height: 120vh;
    font-size: 25%;
    flex-grow: 0;
  }

  #controls {
    flex-shrink: 0;
  }

  #main-content-div {
    width: 95%;
  }

  #display {
    display: -webkit-flex; /* iOS Safari compatibility */
    display: flex;
    -webkit-flex-direction: column; /* iOS Safari compatibility */
    flex-direction: column;
  }

  .player-div {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 50%;
    height: 70%;
    padding: 1rem;
    overflow: hidden;
  }

  .board {
    width: 100%;
    height: 100%;
  }

  .board-row {
    width: 100%;
    height: 10%;
  }

  .square {
    width: 1.2rem;
    height: 1.2rem;
  }

  #sonar {
    width: 60%;
    height: 50%;
  }

  #radar-div {
    padding: 0.1rem;
    width: 30%;
    height: 60%;
    background-color: none;
  }

  #radar {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    box-shadow:
      0 0 0 1px hsla(0, 0%, 0%, 0.5),
      inset 0 0 0 1px hsla(0, 0%, 100%, 0.15);
    background:
      repeating-radial-gradient(
        transparent 0 23px,
        #287a33 24px 26px,
        #0000 27px
      ),
      linear-gradient(transparent 49.75%, #0e4c42 0 50.25%, transparent 0) 50%
        no-repeat,
      linear-gradient(
          90deg,
          transparent 49.75%,
          #0e4c42 0 50.25%,
          transparent 0
        )
        50% no-repeat,
      linear-gradient(#333, #111);
    background-size:
      100%,
      218px 218px,
      218px 218px,
      100%;
    overflow: hidden;
  }

  #radar::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 50%;
    width: 100%;
    height: 100%;
    background: repeating-linear-gradient(
      0deg,
      rgba(0, 0, 0, 0.15),
      rgba(0, 0, 0, 0.15) 1px,
      transparent 1px,
      transparent 2px
    );
  }

  #terminal {
    flex: 0 0 auto; /* prevents growth and shrink */
    min-height: 1px; /* prevents collapse in iOS Safari */
    position: relative; /* if using ::before or ::after pseudo-elements */
  }

  #terminal-span {
    flex-grow: 0;
    overflow: hidden;
    min-width: 1px; /* prevents collapse in iOS Safari */
  }

  #toggle-bomb {
    width: 1rem;
    height: 1rem;
  }
}
